section#nav {
  background-color: #24282f;
  height: 100px;
  color: #fff;
}

section#nav .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

section#nav .logo {
  margin-top: 35px;
}

section#nav .logo h3 {
  font-family: 'NexaFreeBoldwebfont';
  text-transform: uppercase;
  display: inline-block;
  vertical-align: 3px;
  font-size: 2.6rem;
  margin-left: 10px;
}

section#nav .navigation ul li {
  display: inline-block;
  padding: 20px;
  padding-top: 40px;
  list-style-type: none;
}

section#nav .navigation .active {
  border-bottom: 3px solid #b5a263;
  padding-bottom: 38px;
}

section#nav .navigation a {
  text-transform: uppercase;
  color: #ffffff;
  font-size: 1.4rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  text-decoration: none;
}

section#nav .navigation a:hover,
section#nav .navigation a active,
section#nav .navigation a focuse {
  transition: 0.3s;
}

section#nav .navigation a:hover:after,
section#nav .navigation a active:after,
section#nav .navigation a focuse:after {
  content: "";
  background-color: #b5a263;
  display: block;
  width: 100%;
  height: 3px;
  margin: 38px auto;
  transition: 0.3s;
}

.menu-link {
  z-index: 11;
  width: 50px;
  height: 50px;
  display: block;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4);
  position: absolute;
  right: 20px;
  top: 30px;
}

.menu-link span,
.menu-link span:before,
.menu-link span:after {
  display: block;
  height: 2px;
  width: 18px;
  right: 50%;
  margin-right: -9px;
  background-color: #333;
  position: absolute;
  top: 50%;
  margin-top: -1px;
}

.menu-link span:before {
  transition: all 0.2s;
  content: '';
  margin-top: -6px;
}

.menu-link span:after {
  transition: all 0.2s;
  content: '';
  margin-top: 4px;
}

.menu-link_active span {
  background-color: transparent;
}

.menu-link_active span:before {
  margin-top: 0;
  transform: rotate(135deg);
}

.menu-link_active span:after {
  margin-top: 0;
  transform: rotate(-135deg);
}

.menu {
  opacity: 0.9;
  z-index: 10;
  height: 100vh;
  width: 100vw;
  position: fixed;
  background-color: purple;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  right: -120vw;
  transition: all 0.4s;
}

.menu-active {
  right: 0;
}

.menu ul {
  text-align: center;
  list-style: none;
}

.menu ul li {
  width: 100%;
}

.menu ul a {
  text-transform: uppercase;
  text-decoration: none;
  display: block;
  width: 110vw;
  font-weight: 100;
  letter-spacing: 4px;
  line-height: 4;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 1.3rem;
  color: #fff;
  transition: all 0.2s;
}

.menu ul a:hover {
  transition: all 0.2s;
  background-color: rgba(255, 255, 255, 0.4);
}

