.slick-prev,
.slick-next {
  position: absolute;
  outline: none;
  border: none;
  font-size: 0;
  width: 20px;
  height: 30px;
  margin-top: 3rem;
  background-color: transparent;
  cursor: pointer;
}

.slick-prev {
  background-image: url('../img/arrow-left.png');
  background-repeat: no-repeat;
  left: 10rem;
  z-index: 99;
}

.slick-next {
  background-image: url('../img/arrow_right.png');
  background-repeat: no-repeat;
  right: 10rem;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  section#main .main-manager {
    width: calc(33.333333333333336% - 32px);
  }

  section#main .main-manager .manager-social {
    width: calc(100% - 32px);
  }

  section#reviews .reviews-slider .item p {
    width: 80%;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  section#main .main-manager {
    width: calc(100% - 32px);
  }

  section#main .main-manager {
    text-align: center;
  }

  section#main .main-manager img {
    height: 200px;
    width: 200px;
  }

  section#main .main-manager p {
    display: block;
    margin: 0 auto;
  }

  section#main .main-manager .manager-social {
    width: calc(100% - 32px);
  }

  section#reviews .reviews-txt h2 {
    padding-top: 40px;
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (min-width: 666px) and (max-width: 3000px) {
  .menu-link {
    display: none;
  }

  .menu {
    display: none;
  }
}

@media (min-width: 0px) and (max-width: 666px) {
  .navigation {
    display: none;
  }

  .slick-prev,
  .slick-next {
    display: none;
  }

  .item {
    cursor: pointer !important;
  }

  header .header-txt h1 {
    font-size: 2.5rem;
  }
}

@media (min-width: 0px) and (max-width: 720px) {
  section#main {
    height: 1150px;
  }

  section#main .main-txt {
    margin-top: 20px !important;
  }

  section#main .main-manager {
    margin-top: 15px !important;
  }
}